@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon/icomoon.eot?v2q2yq");
  src: url("../fonts/icomoon/icomoon.eot?v2q2yq#iefix") format("embedded-opentype"),
    url("../fonts/icomoon/icomoon.ttf?v2q2yq") format("truetype"),
    url("../fonts/icomoon/icomoon.woff?v2q2yq") format("woff"),
    url("../fonts/icomoon/icomoon.svg?v2q2yq#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  font-family: "icomoon", sans-serif !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-angle-left:before {
  content: "\f104";
}

.icon-angle-right:before {
  content: "\f105";
}

.icon-quote-left:before {
  content: "\f10d";
}

.icon-menu:before {
  content: "\e9bd";
}

.icon-cross:before {
  content: "\ea0f";
}

.icon-arrow-right2:before {
  content: "\ea3c";
}
